<script>

export default {
    name: 'home',

    data: function () {
        return {
            socialIcons: ['linux', 'windows', 'git', 'python', 'java',
            'docker', 'node', 'android', 'apple', 'react', 'vuejs', 'angular',
            'html5', 'css3-alt', 'js-square', 'jenkins', 'gulp', 'grunt', 'aws',
            'phoenix-framework'],
            icons: []
        }
    },

    mounted() {
        this.getIcons()
    },

    methods: {

        getIcons: function() {
            this.icons = this.socialIcons
        },

        getIconClass: function(icon){
            return `fab fa-${icon} fa-2x icon--floating icon--${icon}`
        },

        randomInt: function(min, max) {
            const randomValue = Math.round(Math.random() * (max - min) + min)
            return randomValue
        },

        getIconStyle: function() {
            const speed = `${this.randomInt(3, 9)}000`;
            const position = `${this.randomInt(0, 200)}%`;
            return `left: calc(${position} - 24px); animation-duration: ${speed}ms`
        }
    }
}

</script>
